// í”„ë¡œì íŠ¸ì˜ ìƒíƒœì— ë³€í™”ë¥¼ ì¼ìœ¼í‚¤ëŠ” ê²ƒì„ ì•¡ì…˜ì´ë¼ê³  í•œë‹¤.
// ì•¡ì…˜ ì´ë¦„ì€ ë¬¸ìì—´ í˜•íƒœë¡œ, ì£¼ë¡œ ëŒ€ë¬¸ìë¡œ ì‘ì„±í•˜ë©° ì•¡ì…˜ ì´ë¦„ì€ ê³ ìœ í•´ì•¼ í•œë‹¤.
const TOGGLE_SWITCH = 'TOGGLE_SWITCH';
const INCREASE = 'INCREASE';
const DECREASE = 'DECREASE';

// ì•¡ì…˜ ê°ì²´ëŠ” type ê°’ì„ ë°˜ë“œì‹œ ê°–ê³  ìˆì–´ì•¼ í•˜ë©°, ê·¸ ì™¸ì— ì¶”í›„ ìƒíƒœë¥¼ ì—…ë°ì´íŠ¸í•  ë•Œ ì°¸ê³ í•˜ê³  ì‹¶ì€ ê°’ì€ ë§ˆìŒëŒ€ë¡œ ë„£ì„ ìˆ˜ ìˆë‹¤.
const toggle_switch = () => ({ type: TOGGLE_SWITCH });
const increase = difference => ({ type: INCREASE, difference });
