// ì´ í”„ë¡œì íŠ¸ì—ì„œ ì‚¬ìš©í•  ì´ˆê¸°ê°’ì„ ì •ì˜í•œë‹¤. ì´ˆê¸°ê°’ì˜ í˜•íƒœëŠ” ììœ (ìˆ«ì, ë¬¸ìì—´, ê°ì²´ ëª¨ë‘ ê°€ëŠ¥)ë‹¤.
const initialState = {
    toggle: false,
    counter: 0
};

// ë¦¬ë“€ì„œëŠ” ë³€í™”ë¥¼ ì¼ìœ¼í‚¤ëŠ” í•¨ìˆ˜ë‹¤. í•¨ìˆ˜ì˜ íŒŒë¼ë¯¸í„°ë¡œëŠ” stateì™€ action ê°’ì„ ë°›ì•„ ì˜¨ë‹¤.
// stateê°€ undefinedì¼ ë•ŒëŠ”, initialStateë¥¼ ê¸°ë³¸ê°’ìœ¼ë¡œ ì‚¬ìš©
function reducer(state = initialState, action) {
    // action.typeì— ë”°ë¼ ë‹¤ë¥¸ ì‘ì—…ì„ ì²˜ë¦¬í•œë‹¤.
    switch (action.type) {
        case TOGGLE_SWITCH:
            return {
                // spread ì—°ì‚°ì(...)ë¥¼ ì‚¬ìš©í•˜ì—¬ ë¶ˆë³€ì„± ìœ ì§€ë¥¼ í•´ ì¤˜ì•¼ í•œë‹¤.
                // ë‹¨ ê°ì²´ì˜ êµ¬ì¡°ê°€ ë³µì¡í•´ì§€ë©´(ì˜ˆë¥¼ ë“¤ì–´ object.something.inside.value) spread ì—°ì‚°ìë¡œ ë¶ˆë³€ì„±ì„ ê´€ë¦¬í•˜ë©° ì—…ë°ì´íŠ¸ í•˜ëŠ” ê²ƒì´
                // êµ‰ì¥íˆ ë²ˆê±°ë¡œìš¸ ìˆ˜ ìˆê³  ì½”ë“œì˜ ê°€ë…ì„±ë„ ë‚˜ë¹ ì§€ê¸° ë•Œë¬¸ì— ë¦¬ë•ìŠ¤ì˜ ìƒíƒœëŠ” ìµœëŒ€í•œ ê¹Šì§€ ì•Šì€ êµ¬ì¡°ë¡œ ì§„í–‰í•˜ëŠ” ê²ƒì´ ì¢‹ë‹¤.
                // ê°ì²´ì˜ êµ¬ì¡°ê°€ ë³µì¡í•´ì§€ê±°ë‚˜ ë°°ì—´ë„ í•¨ê»˜ ë‹¤ë£¨ëŠ” ê²½ìš° immer ë¼ì´ë¸Œë¼ë¦¬ë¥¼ ì‚¬ìš©í•˜ë©´ ì¢€ ë” ì‰½ê²Œ ë¦¬ë“€ì„œë¥¼ ì‘ì„±í•  ìˆ˜ ìˆë‹¤.
                ...state,
                toggle: !state.toggle
            };
        case INCREASE:
            return {
                ...state,
                toggle: state.counter + action.difference
            };
        case DECREASE:
            return {
                ...state,
                toggle: state.counter - 1
            };
        default:
            return state;
    }
}